.aw-communication-page {
  max-width: 700px;
}

.aw-communication-page .aw-communication-form__email-field {
  max-width: 480px;
  margin: 25px 0 0;
}
@media (min-width: 900px) {
  .aw-communication-page .aw-communication-form__email-field {
    margin: 27px 30px 0;
  }
}
.aw-communication-page .aw-communication-form__preference-list {
  margin: 20px 0 20px;
}
@media (min-width: 900px) {
  .aw-communication-page .aw-communication-form__preference-list {
    margin: 20px 15px 20px;
  }
}
.aw-communication-page .aw-communication-form__preference {
  padding: 17px 18px;
}
.aw-communication-page .aw-communication-form__preference--highlighted {
  background: hsla(0deg, 0%, 50%, 0.07);
  border-radius: 5px;
}
.aw-communication-page .aw-communication-form__preference-inner {
  position: relative;
}
.aw-communication-page .aw-communication-form__preference-checkbox {
  position: absolute;
  top: 4px;
  left: 0;
}
.aw-communication-page .aw-communication-form__preference-text {
  padding-left: 30px;
}
.aw-communication-page .aw-communication-form__preference-title {
  display: inline-block;
  font-size: 1.31575em;
  line-height: 1.1;
  margin: 0 0 0.2em;
}
.aw-communication-page .aw-communication-form__preference-description {
  margin: 0;
  font-style: italic;
}
.aw-communication-page .aw-communication-form hr {
  margin: 25px 0;
}
.aw-communication-page .aw-communication-form__preference--all .aw-communication-page .aw-communication-form__preference-title {
  font-size: 1em;
}
.aw-communication-page .aw-communication-form__terms-text {
  margin: 20px 0 30px;
}